<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                           {{$t('AddAccount')}}
                        </h4>
                        <button class="close" @click="ClosePopup"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="IntegrateAccount">
                            <Field name="Name" v-slot="{errors}" :rules="{ 'required': true }" >
                                <div class="form-group">
                                    <label for="Name">{{$t('Name')}}:</label><span class="text-danger">*</span>
                                    <input type="text" placeholder="Enter Name" v-model="Name" :class="{'form-control':true,'is-invalid':errors[0]}">
                                    <span v-if="errors[0]" name="Description" class="invalid-feedback d-block">{{ errors[0] }}</span>
                                </div>
                            </Field> 
                            <div class="card" style="width: 18rem;">
                                <img class="card-img-top" src="https://www.coriniumtech.com/wp-content/uploads/2017/10/OneDrive-800x400.jpg" alt="Card image cap">
                            </div>
                        </Form>
                    </div>
                    <div class="modal-footer text-right">
                        <button title="Search" id="AddFolder"  @click="InterGrateAccount('OneDrive')" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2">
                            <em class="fa fa-floppy-o pr-2"></em> {{$t('Save')}}
                        </button>
                        <button title="Cancel" @click="ClosePopup" class="popupredbtn btn btn-danger formbtn widthhalf pr-2">
                            <em aria-hidden="true" class="fa fa-times pr-2"></em> {{$t('Cancel')}}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from "./../../../services/DataService";
export default {
  methods:{
    ClosePopup:function(){
        var vm=this;
        vm.$emit("ClosePopup");
    },
    InterGrateAccount:function(params){
        var vm=this;
        vm.$refs.IntegrateAccount.validate().then(result => {
            if(result){
                var params=`name=${vm.Name}`
                DataService.GetAuthorizationUrl(params).then((response) => {
                    debugger;
                    if(response.data.exist==vm.Name){
                        vm.ShowAlert(vm.$t("AlreadyExist"),'warning')
                    }else{
                        window.location.href=response.data.authorizationUrl;
                    }
                })
            }
        })
        
    }
  }
}
</script>